}
@font-face {
  font-family: "Nexa";
  font-weight: 300;
  src: url(/src/fonts/Nexa-Trial-Light.otf);
}
.form__type-of-shipping__container {
  display: grid;
  gap: 20px;
}

@media screen and (min-width: 1024px) {
  .address-info__item:first-of-type {
    width: 100%;
    grid-column-start: 1;
    grid-column-end: 3;
  }
  .address-info__item:last-of-type {
    width: 100%;
    grid-column-start: 3;
    grid-column-end: 5;
  }
}

