@import '../../style/variables.scss';

.small-product {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  &__img {
    width: 66px;
    height: 66px;
    margin-bottom: 10px;
    border-radius: 50%;
  }
  &__name {
    font-size: 14px;
    font-weight: 500;
    line-height: 17.37px;

    margin-bottom: 11px;
  }
  &__footer {
    display: flex;
    width: 100%;
    justify-content: center;
  }
  &__price {
    font-size: 18px;
    font-weight: 700;
    line-height: 22.54px;

    margin-right: 10px;
  }
}

@media screen and (max-width: 359px) {
  .small-product {
    &__price {
      font-size: 14px;
    }
  }
}

@media screen and (min-width: 768px) {
  .small-product {
    &__img {
      width: 132px;
      height: 132px;
    }
  }
}
